<template>
    <div class="order">
        <div class="order-nav">
            <ul>
                <li :class="{active:tab_select==1}"><a @click="$router.push({query:{tab:1}})">全部订单</a></li>
                <li :class="{active:tab_select==2}"><a @click="$router.push({query:{tab:2}})">待付款</a></li>
                <li :class="{active:tab_select==3}"><a @click="$router.push({query:{tab:3}})">待发货</a></li>
                <li :class="{active:tab_select==4}"><a @click="$router.push({query:{tab:4}})">待收货</a></li>
                <li :class="{active:tab_select==5}"><a @click="$router.push({query:{tab:5}})">已完成</a></li>
                <li :class="{active:tab_select==6}"><a @click="$router.push({query:{tab:6}})">已取消</a></li>
            </ul>
        </div>
        <div class="order-filter">
            <table class="ac">
                <tr>
                    <td class="info">
                        从：
                        <date :tag="`start`"></date>
                        至：
                        <date :tag="`end`"></date>
                    </td>
                    <td class="price">单价</td>
                    <td class="Quantity">数量</td>
                    <td class="receiver">收货人</td>
                    <td class="total-price">支付总金额</td>
                    <td class="order-status">
                        <!--<select class="ac">-->
                        <!--<option value="">全部订单</option>-->
                        <!--<option value="">待支付</option>-->
                        <!--<option value="">待发货</option>-->
                        <!--<option value="">待收货</option>-->
                        <!--<option value="">已完成</option>-->
                        <!--<option value="">已取消</option>-->
                        <!--</select>-->
                        <!--<a class="fr button" @click="getOrderList(-1)">确定</a>-->
                    </td>
                    <td class="control">
                        操作
                    </td>
                </tr>
            </table>
            <div class="clearfix page-control">
                <input v-if="tab==2" type="checkbox" v-model="allSelect">
                <span v-if="tab==2">全选</span>
                <a v-if="tab==2" class="button" style="margin-left: 30px" @click="allPay">合并付款</a>
                <a class="fr button" @click="next(1)" style="margin-left:10px">下一页</a>
                <div class="fr" style="margin-left: 10px">{{pageInfo.current_page}}/{{pageInfo.pages}}</div>
                <a class="fr button" @click="next(-1)">上一页</a>
            </div>
        </div>
        <div class="order-page">
            <OrderTable v-if="lists.length>0" v-for="(i,k) in lists" :key="k" :index="k" :exchange="exchange"
                        :order_info="i"></OrderTable>
            <div v-if="lists.length===0" class="ac" style="padding-top: 50px;">
                <img src="../assets/img/nothing/order.png" alt="">
                <p class="bold">怎么回事？居然一个订单也没有。</p>
            </div>
        </div>
    </div>
</template>

<script>
    let display = {
        showRecommend: true,
        nav: 'default',
        header: 'default'
    };
    import date from '../components/date'
    import OrderTable from '../components/order-table'
    import {mapState} from 'vuex'

    export default {
        name: "order",
        metaInfo: {
            title: '我的订单-个人中心', // set a title
            meta: [{                 // set meta
                name: '福分通',
                content: '专注企业员工福利'
            }]
        },
        created() {
            this.$store.dispatch('layout/Change', display);
            if (this.$route.query.tab) {
                this.tab = this.$route.query.tab;
                this.getOrderList(parseInt(this.$route.query.tab));
            } else {
                this.getOrderList(1)
            }
        },
        data() {
            return {
                current_tab: 1,
                tab: 1,
                lists: [],
                limit: 10,
                pageInfo: {
                    current_page: 1,
                },
                exchange: 1,
                unit: ''
            }
        },
        computed: {
            ...mapState('order', ['ToBePaid', 'start_time', 'end_time']),
            allSelect: {
                get() {
                    if (this.ToBePaid.length === this.lists.length) {
                        return true
                    } else {
                        return false
                    }
                },
                set() {
                    if (this.ToBePaid.length === this.lists.length) {
                        this.$store.dispatch('order/DeleteAll')
                    } else {
                        for (let i = 0; i < this.lists.length; i++) {
                            this.$store.dispatch('order/Insert', this.lists[i].order_sn)
                        }
                    }
                }
            },
            tab_select: {
                get() {
                    return this.$route.query.tab
                }
            }
        },
        watch: {
            start_time: {
                handler() {
                    this.pageInfo.current_page = 1;
                    this.getOrderList(this.tab)
                }
            },
            end_time: {
                handler() {
                    this.pageInfo.current_page = 1;
                    this.getOrderList(this.tab)
                }
            },
            tab_select: {
                handler(e) {
                    this.changeTab(e)
                }
            }
        },
        methods: {
            /**
             * 切换TAB标签
             * */
            changeTab(id) {
                if (id !== -1) {
                    this.current_tab = id;
                }
                this.tab = id;
                // this.$router.push({
                //     query: {
                //         tab: id
                //     }
                // })
                this.pageInfo.current_page = 1;
                this.getOrderList(id);
            },
            getOrderList(ids) {

                // <div><a v-if="order_info.status == 0 && order_info.shipping_status == 0">未付款</a></div>
                // <div><a v-if="order_info.status == 1 && order_info.shipping_status == 0">已付款</a></div>
                // <div><a v-if="order_info.status == 1 && order_info.shipping_status == 1">配货中</a></div>
                // <div><a v-if="order_info.status == 1 && order_info.shipping_status == 2">已发货</a></div>
                // <div><a v-if="order_info.status == 1 && order_info.shipping_status == 3">已收货</a></div>
                // <div><a v-if="order_info.status == 5 && order_info.shipping_status == 5">已结算</a></div>
                // <div><a v-if="order_info.status == 4 && order_info.shipping_status == 4">已退货</a></div>
                // <div><a v-if="order_info.status == 7 && order_info.shipping_status == 4">已换货</a></div>
                // <div><a v-if="order_info.status == 2">已取消</a></div>

                let id = parseInt(ids);
                let status = -1;
                let shipping_status = -1;
                let pay_status = -1;
                // 全部订单
                if (id === 1) {

                } else if (id === 2) {
                    // 待付款
                    status = 1;
                    shipping_status = 0;
                    pay_status = 0;
                } else if (id === 3) {
                    // 待发货
                    status = 1;
                    shipping_status = 0;
                    pay_status = 1;
                } else if (id === 4) {
                    // 待收货
                    status = 1;
                    shipping_status = 2;
                    pay_status = 1;
                } else if (id === 5) {
                    // 已完成
                    status = 1;
                    shipping_status = 3;
                    pay_status = 1;
                } else if (id === 6) {
                    // 已取消
                    status = 2;
                }


                let data = {
                    page: this.pageInfo.current_page,
                    limit: this.limit,
                    status: status,
                    start_time: Date.parse(this.start_time) / 1000,
                    end_time: Date.parse(this.end_time) / 1000 + 57600,
                    shipping: shipping_status,
                    pay_status: pay_status
                };
                this.lists = [];
                this.bus.$emit('loading', '加载中', false);
                this.yum('/api/order/listss', data).then(res => {
                    if (res.data.status === 200) {
                        setTimeout(() => {
                            this.bus.$emit('loading', '加载中', true);
                        }, 300)
                        this.lists = res.data.data;
                        this.pageInfo = res.data.page;
                        this.exchange = res.data.exchange;
                        this.exchange = res.data.exchange;
                        this.unit = res.data.unit;
                    } else {
                        setTimeout(() => {
                            this.bus.$emit('loading', '加载中', true);
                            this.bus.$emit('notice', res.data.msg)
                        }, 300)
                        this.lists = res.data.data;
                    }
                }).catch(err => {
                    this.bus.$emit('notice', '网络错误')
                });
            },
            next(p) {
                if (this.pageInfo.current_page == 1 && p < 0) {
                    this.pageInfo.current_page = 1
                } else if (this.pageInfo.current_page == this.pageInfo.pages && p > 0) {
                    this.pageInfo.current_page = this.pageInfo.pages
                } else {
                    this.pageInfo.current_page = parseInt(this.pageInfo.current_page) + p;
                    this.getOrderList(this.tab);
                }

            },
            allPay() {
                if (this.ToBePaid.length === 0) {
                    return false
                }
                let data = {
                    path: '/paytable', query: {
                        order_sn: this.ToBePaid
                    }
                };
                this.$router.push(data)
            }
        },
        components: {
            date, OrderTable
        }
    }
</script>

<style scoped lang="stylus">
    .order
        position relative
        &-nav
            position relative
            height 50px
            line-height 50px
            background-color white
            box-shadow 0 0 8px #bbb
            ul
                display inline-block
                line-height 50px
                height 50px
                li
                    display inline-block
                    height 49px
                    line-height 48px
                    padding 0 20px
                    border-bottom 2px solid white
                    position relative
                    a
                        display block
                    &:hover, &.active
                        border-bottom 2px solid #ff0077
                        a
                            color #ff0077
                    &:after
                        content ''
                        position absolute
                        display inline-block
                        border-right 2px solid #ccc
                        height 16px
                        right 0
                        top 16px
        &-filter
            position relative
            margin-top 10px
            width 820px
            table
                width 100%
                height 36px
                background-color #e4e4e4
                tr
                    height 36px
                td.control
                    width 110px
                td.order-status
                    width 90px
                    select
                        background-color rgba(0, 0, 0, 0)
                        border none
                td.total-price
                    width 90px
                td.receiver
                    width 60px
                td.Quantity
                    width 40px
                td.price
                    width 90px
            .page-control
                height 26px
                line-height 26px
                margin-top 20px
                padding-left 20px
                position relative
                input[type=checkbox]
                    width 16px
                    height 16px
                    vertical-align middle
                a.button
                    display inline-block
                    padding 6px 12px
                    border solid 1px #ccc
                    border-radius 4px
                    line-height 12px
                    user-select none
                    background-color white
        &-page
            position relative
            margin-top 10px
</style>
