<template>
    <div class="date">
        <input v-if="tag==='start'" type="date" v-model="StartTime">
        <input v-if="tag==='end'" type="date" v-model="EndTime">
    </div>
</template>

<script>
    import {mapState} from 'vuex'

    export default {
        name: 'date',
        data() {
            return {
                // date:'2018-01-01'
            }
        },
        props: {
            tag: String
        },
        methods: {
            choosed() {
                let timestamp = Date.parse(this.date)
            }
        },
        computed: {
            ...mapState('order', ['start_time', 'end_time']),
            StartTime: {
                get() {
                    return this.start_time
                },
                set(e) {
                    this.$store.dispatch('order/setStart', e)
                }
            },
            EndTime: {
                get() {
                    return this.end_time
                },
                set(e) {
                    this.$store.dispatch('order/setEnd', e)
                }
            },

        },
        components: {}
    }
</script>

<style lang="stylus" scoped>
    .date
        display inline-block
        width 120px
        position relative
        input
            width 120px
            background-color white
            &::-webkit-inner-spin-button
                display none
</style>
