<template>
    <div class="order-table">
        <div class="order-title">
            <input type="checkbox" v-model="selected">
            <p>下单时间：<span style="margin-right: 30px">{{order_info.add_time}}</span>订单号：<span>{{order_info.order_sn}}</span></p>
        </div>
        <table class="ac">
            <tr  v-for="(goods,k) in order_info.goods_child ">
                <td class="info">
                    <div class="clearfix">
                        <a :href="`/goods.html#/${goods.product_id}`" target="_blank">
                            <img class="fl" v-bind:src="goods.default_img | img"
                                 alt="">
                            <span class="fl al">{{goods.product_name}}</span>
                        </a>
                    </div>
                    <div class="ar">
                        <a v-if="goods.return_status == 0&&order_info.pay_status==1&&order_info.shipping_status!=0" @click="ReturnGoods(goods , order_info.order_sn)">退换/售后</a>
                        <a v-if="goods.return_status == 1" @click="ReturnGoods(goods , order_info.order_sn)">退货中</a>
                        <a v-if="goods.return_status == 2" @click="ReturnGoods(goods , order_info.order_sn)">已退货</a>
                    </div>
                </td>
                <td class="price"><span style="color: black;font-size: 14px;">{{parseInt(goods.goods_point) | change_unit }}{{storage.getStorage('exchange').unit}}</span></td>
                <td class="Quantity">{{goods.nums}}</td>
                <td v-if="k===0" class="receiver" :rowspan="order_info.goods_child.length">{{order_info.shouhuo_name}}</td>
                <td v-if="k===0" class="total-price" :rowspan="order_info.goods_child.length">
                    <span style="font-size: 14px;font-weight: bold;color: black">{{parseInt(order_info.subtotal) | change_unit}}{{storage.getStorage('exchange').unit}}</span>
                    <br>
                    （含运费）
                </td>
                <td v-if="k===0" class="order-status" :rowspan="order_info.goods_child.length">
                    <div><a v-if="order_info.pay_status==0 && order_info.status != 2">未付款</a></div>
                    <div><a v-if="order_info.status == 1 && order_info.shipping_status == 0 && order_info.pay_status == 1">待发货</a></div>
                    <div><a v-if="order_info.status == 1 && order_info.shipping_status == 2 && order_info.pay_status == 1">已发货</a></div>
                    <div><a v-if="order_info.status == 1 && order_info.shipping_status == 3 && order_info.pay_status == 1">已收货</a></div>
                    <div><a v-if="order_info.status == 5 && order_info.shipping_status == 3 && order_info.pay_status == 1">已结算</a></div>
                    <div><a v-if="order_info.status == 4 && order_info.shipping_status == 4 && order_info.pay_status == 1">已退货</a></div>
                    <div><a v-if="order_info.status == 7 && order_info.shipping_status == 4 && order_info.pay_status == 1">已换货</a></div>
                    <div><a v-if="order_info.status == 2">已取消</a></div>
                    <div><router-link :to="{
                        path:'/order/logistics',
                        query:{order_id:order_info.order_id}
                    }">物流信息</router-link></div>
                    <div>
                        <router-link :to="{path:'/order/detail',query:{order_sn:order_info.order_sn} }">订单详情</router-link>
                    </div>
                </td>
                <td v-if="k===0" class="control" :rowspan="order_info.goods_child.length">
                    <div><a v-if="order_info.pay_status==0 && order_info.status != 2" title="是否去支付？">未付款</a></div>
                    <div><router-link
                        :to="{path:'/paytable',query:{
                            order_sn:order_info.order_sn
                        }}"
                        class="orange_btn"
                        v-if="order_info.pay_status == 0 && order_info.status != 2" title="是否去支付？">立即支付</router-link></div>
                    <div><a
                        v-if="order_info.pay_status == 0 && order_info.status != 2"
                        @click="cancelOrder(order_info.order_id)"
                        title="是否取消订单？">取消订单</a></div>

                    <div><a v-if="order_info.status == 1 && order_info.shipping_status == 0 && order_info.pay_status == 1">待发货</a></div>
                    <div><a v-if="order_info.status == 1 && order_info.shipping_status == 2 && order_info.pay_status == 1">已发货</a></div>
                    <div><a v-if="order_info.status == 1 && order_info.shipping_status == 3 && order_info.pay_status == 1">已收货</a></div>
                    <div><a v-if="order_info.status == 5 && order_info.shipping_status == 3 && order_info.pay_status == 1">已结算</a></div>
                    <div><a v-if="order_info.status == 4 && order_info.shipping_status == 4 && order_info.pay_status == 1">已退货</a></div>
                    <div><a v-if="order_info.status == 7 && order_info.shipping_status == 4 && order_info.pay_status == 1">已换货</a></div>
                    <div><a v-if="order_info.status == 2">已取消</a></div>
                    <div><a v-if="order_info.pay_status == 1 && order_info.shipping_status == 2" class="blue_btn" @click="comfirmReceive(order_info.order_id)">确认收货</a></div>
                    <div><a v-if="goods.return_status == 0&&order_info.pay_status==1&&order_info.shipping_status!=0" @click="ReturnGoodsAll(order_info)">退换/售后</a></div>

                </td>
            </tr>
        </table>
    </div>
</template>

<script>
    import { mapState } from 'vuex'

    export default {
        name: 'order-table',
        props: {
            index: Number,
            order_info:{},
            exchange:''
        },
        data() {
            return {}
        },
        watch: {},
        computed: {
            ...mapState('order', ['ToBePaid']),
            selected: {
                get() {
                    if (this.ToBePaid.indexOf(this.order_info.order_sn) === -1) {
                        return false
                    } else {
                        return true
                    }
                },
                set(e) {
                    if (e) {
                        this.$store.dispatch('order/Insert', this.order_info.order_sn)
                    } else {
                        this.$store.dispatch('order/Delete', this.order_info.order_sn)
                    }
                }
            }
        },
        methods:{
            ReturnGoods(goods , orderSn){
                this.$router.push(
                    {path:'/order/return' , query:{id:goods.product_id,order_sn:orderSn}}
                );
                this.$store.dispatch('returnGoods/Insert',goods)
            },
            ReturnGoodsAll(i){
                let goods = [];
                let orderSn = i.order_sn;
                i.goods_child.forEach( item =>{
                    goods.push(item.product_id)
                });
                this.$router.push(
                    {path:'/order/return' , query:{id:goods,order_sn:orderSn}}
                );
            },
            cancelOrder(id){
              let url='api/Order/chancelMemberOrder';
              let data = {order_id:id};
              this.yum(url,data).then(res=>{
                  this.bus.$emit('notice',res.data.msg);
                  this.$router.go(0)
              })
            },
            comfirmReceive(id){
                this.yum('api/Order/confirmReceive',{order_id:id}).then(res=>{
                    this.bus.$emit('notice',res.data.msg);
                    this.$router.go(0)
                })
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .order-table
        position relative
        width 820px
        margin-bottom 10px
        .order-title
            height 36px
            background-color #e4e4e4
            line-height 36px
            padding-left 20px
            input[type=checkbox]
                width 16px
                height 16px
                outline none
                vertical-align middle
            p
                display inline-block
                margin-left 10px
        table
            width 100%
            font-size 12px
            background-color white
            tr
                height 36px
            td
                border 1px #ccc solid
                font-size 12px
            td.info
                padding 20px
                img
                    display inline-block
                    width 60px
                    height 60px
                span
                    width 220px
                    line-height 25px
                    margin-left 10px
            td.control
                width 110px
            td.order-status
                width 90px
            td.total-price
                width 90px
            td.receiver
                width 60px
            td.Quantity
                width 40px
            td.price
                width 90px
    .orange_btn
        padding 5px 10px
        display inline-block
        background-color #ff6633
        color white
        border-radius 4px
        margin 5px auto
    .blue_btn
        padding 5px 10px
        display inline-block
        background-color #3399ff
        color white
        border-radius 4px
        margin 5px auto
</style>
